<template>
  <el-dialog
    :title="'新增'"
    :close-on-click-modal="false"
    :visible.sync="visible"
  >
    <el-form :model="TbUserForm" :rules="dataRule" ref="TbUserForm" @keyup.enter.native="TbUserFormSubmit()" label-width="80px">
      <el-form-item label="UserId" prop="userId">
        <el-input v-model="TbUserForm.userId"
                  placeholder="UserId"
        ></el-input>
      </el-form-item>
      <el-form-item label="用户名" prop="username">
        <el-input v-model="TbUserForm.username"
                  placeholder="用户名"
        ></el-input>
      </el-form-item>
      <el-form-item label="手机号" prop="mobile">
        <el-input v-model="TbUserForm.mobile"
                  placeholder="手机号"
        ></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="TbUserForm.password"
                  placeholder="密码"
        ></el-input>
      </el-form-item>
      <el-form-item label="创建时间" prop="createTime">
        <el-date-picker
          v-model="TbUserForm.createTime"
          value-format="yyyy-MM-dd"
          :readonly="false"
          :disabled="false"
          :editable="true"
          :clearable="true">
        </el-date-picker>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="TbUserFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
  export default {
    data () {
      let validatepassword = (rule, value, callback) => {
        if (!/^1[34578]\d{9}$/.test(value)) {
          callback(new Error('密码格式不正确'))
        } else {
          callback()
        }
      }
      return {
        visible: false,
        TbUserForm: {
          userId: '',
          username: '',
          mobile: '',
          password: '',
          test: ''
        },
        dataRule: {
          password: [{validator: validatepassword, trigger: 'blur'}, {required: true, message: '密码必须填写', trigger: 'blur'}
          ],
          test: [{required: true, message: '日期选择器不能为空', trigger: 'blur'}] }
      }
    },
    methods: {
      init (id) {
        this.TbUserForm.userId = id || 0
        this.visible = true
        this.$nextTick(() => {
          this.$refs['TbUserForm'].resetFields()
          if (this.TbUserForm.userId) {
            this.$http.get(this.$http.adornUrl(`/tbuser/info`), {params: this.$http.adornParams({userId: this.TbUserForm.userId})}).then(({data}) => {
              if (data && data.code === 0) {
                for (let key in data.tbuser) {
                  if (data.tbuser[key]) {
                    this.TbUserForm[key] = data.tbuser[key]
                  }
                }
              }
            })
          }
        })
      },
      // 表单提交
      TbUserFormSubmit () {
        this.$refs['TbUserForm'].validate((valid) => {
          if (valid) {
            this.$http.post(this.$http.adornUrl(`/tbuser/${!this.TbUserForm.userId ? 'add' : 'update'}`), this.$http.adornData(this.TbUserForm)).then(({data}) => {
              if (data && data.code === 0) {
                this.$message({
                  message: '操作成功',
                  type: 'success',
                  duration: 1500,
                  onClose: () => {
                    this.visible = false
                    this.$emit('refreshDataList')
                  }
                })
              } else {
                this.$message.error(data.msg)
              }
            })
          }
        })
      }
    }
  }
</script>



